.navbar {
  background: #fff;
  padding: 1rem 0;
  border-bottom: 1px solid #e5e5e5;
  position: relative;
}

.menuButton {
  display: none;
  background: none;
  border: none;
  font-size: 2rem;
  color: #111;
  cursor: pointer;
  position: absolute;
  right: 1.5rem;
  top: 1rem;
  z-index: 2;
}

.menuIcon {
  display: inline-block;
  font-size: 2rem;
  line-height: 1;
}

.navList {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin: 0;
  padding: 0;
  transition: max-height 0.3s;
}

@media (max-width: 700px) {
  .menuButton {
    display: block;
  }
  .navList {
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
    background: #fff;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    max-height: 0;
    overflow: hidden;
    border-bottom: 1px solid #e5e5e5;
    z-index: 1;
  }
  .navList.open {
    max-height: 300px;
    padding-bottom: 1rem;
    border-bottom: 1px solid #e5e5e5;
  }
}

.navLink {
  color: #111;
  text-decoration: none;
  font-weight: 500;
  transition: border-bottom 0.2s;
  border-bottom: 2px solid transparent;
  padding-bottom: 2px;
}

.navLink:hover {
  border-bottom: 2px solid #111;
}

@media (max-width: 600px) {
  .navList {
    flex-direction: column;
    gap: 1rem;
  }
} 