.contactForm {
  background: #fff;
  margin: 2rem 0;
  padding: 1.5rem;
  border-radius: 0;
  border: 1px solid #e5e5e5;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.formTitle {
  color: #111;
  margin-bottom: 1rem;
  text-align: left;
  font-weight: bold;
}

.label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-weight: bold;
  color: #111;
  gap: 0.5rem;
}

.input, .textarea {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #e5e5e5;
  border-radius: 0;
  font-size: 1rem;
  margin-top: 0.25rem;
  background: #fff;
  color: #111;
}

.textarea {
  min-height: 100px;
  resize: vertical;
}

.button {
  background: #111;
  color: #fff;
  border: 1px solid #111;
  padding: 0.5rem 1.5rem;
  border-radius: 0;
  font-size: 1rem;
  cursor: pointer;
  align-self: flex-end;
  transition: background 0.2s, color 0.2s;
}

.button:hover {
  background: #fff;
  color: #111;
}

.error {
  color: #d32f2f;
  background: #fff;
  border: 1px solid #d32f2f;
  padding: 0.5rem 1rem;
  border-radius: 0;
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

@media (max-width: 600px) {
  .contactForm {
    padding: 1rem;
  }
  .button {
    width: 100%;
    align-self: stretch;
  }
} 